* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  background-color: #cbb5b5;
  padding: 20px;
}

.container {
  max-width: 1200px;
  margin: auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

#productCardContainer {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-content: center;
  gap: 20px;
  display: grid;
}

.product-card {
  text-align: center;
  background: #fff;
  border-radius: 10px;
  padding: 15px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 4px 6px #0000001a;
}

.product-card:hover {
  transform: scale(1.05);
}

.product-card img {
  object-fit: cover;
  border-radius: 5px;
  max-width: 100%;
  height: 150px;
}

.product-card h2 {
  margin: 10px 0;
  font-size: 18px;
}

.product-card p {
  color: #555;
  font-size: 16px;
}

.stock {
  color: #27ae60;
  font-weight: bold;
}

.product-card p {
  color: #e74c3c;
  font-size: 18px;
  font-weight: bold;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  transition: background .2s;
}

button:hover {
  background-color: #2980b9;
}

#filterForm {
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

#filterForm select, #filterForm input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

@media (width <= 600px) {
  #filterForm {
    flex-direction: column;
    align-items: center;
  }
}
